html{
    scroll-behavior: smooth;
    scroll-padding-top: 50px;
}
.aboutus {
    width: auto;
    height: 500px;
     /* Default margin for larger screens */

    /* Media query to remove margin on screens smaller than 640px (adjust as needed) */
    @media (max-width: 640px) {
        margin-left: 0;
    }
}
.textaboutus{
    text-align: justify;
    font-size: medium;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.titreaboutus{
    font-weight: bold;
    margin-bottom: 15px;
    font-size: 45px;
}
.card:hover{
    transform: scale(1.05);transition: transform 0.3s ease-in-out;
}

.titre{
    font-size: 17px;
    font-weight: bold;
}
@media (max-width: 768px) {
  
}

.aboutuss {
    gap: 300px;
  }
  
  /* Adjust gap for small screens */
  @media (max-width: 640px) {
    .aboutuss {
      gap: 18px;
    }
    .form{
        margin-left: 18px;
    }
  }

  .gradient-text {
    background: linear-gradient(90deg, #2967A3, #26A8DF);
    -webkit-background-clip: text;
    color: transparent;
  }